          class="flex items-center p-4 px-5 rounded-lg qr-gray-bg mb-4"
        >
          <img src="@/assets/icons/money.svg" class="list-icon mr-4" />
          <div class>
            <span
              class="text-sm text-white tracking-wide truncate w-32 inline-block"
            >{{transaction.user.name}}</span>
            <span
              class="text-sm text-white tracking-wide truncate block"
            >{{transaction.transaction_customer_name}}</span>
            <span
              class="block text-xs text-gray-500 tracking-wide"
            >{{transaction.created_at | formatDate}}</span>
          </div>
          <div class="ml-auto">
            <span
              :class="{'font-semibold tracking-wider text-sm':true,'text-white':true, 'text-green-500':false}"
            >{{transaction.transaction_amount}} <i class="text-xs">Ksh</i></span>
          </div>
        </router-link>

        <!-- Loop end -->
      </div>
    </div>
    <!-- User Merchant End-->

    <!-- Merchant Business List -->
    <transition name="popup">
      <div
        v-if="showMerchantBusinesses"
        class="absolute top-0 left-0 p-5 h-screen oveflowy-auto w-full overflow-y-auto"
      >
        <div class="p-1 pb-16">
          <div class="pb-5 flex justify-between">
            <button @click="showMerchantBusinesses = false; lockAppScroll = false">
              <img src="@/assets/icons/close-white.svg" class="w-6" />
            </button>
          </div>

          <div
            class="flex items-center p-4 px-5 rounded-lg qr-gray-bg mb-4"
            v-for="business in businesses"
            :key="business.id"
          >
            <router-link to="#" class="inline-flex items-center">
              <img src="@/assets/icons/shop.svg" class="list-icon mr-4" />
              <div class>
                <span
                  class="text-sm text-gray-500 tracking-wide truncate w-24 inline-block"
                >{{business.business_name}}</span>
                <span
                  class="block text-xs text-gray-500 tracking-wide"
                >{{business.business_location}}</span>
              </div>
            </router-link>
            <div class="ml-auto">
              <button
                @click="changeActiveBusiness(business)"
                class="font-semibold tracking-wider text-xs rounded-full text-white qr-btn py-1 px-3 flex justify-center items-center"
              >Select</button>
            </div>
          </div>

          <!-- Add New Business Button -->
          <div class="fixed bottom-0 left-0 pb-4 flex justify-center items-center w-full">
            <button
              @click="showAddNewBusinessForm = true"
              class="flex items-center font-semibold tracking-wider text-xs rounded-full text-white qr-btn py-2 px-4 flex justify-center items-center"
            >
              <span>Add Business</span>
              <img src="@/assets/icons/plus.svg" class="icon-after-span" />
            </button>
          </div>
        </div>
      </div>
    </transition>
    <!--  Merchant Business List End -->

    <!-- Add New Business -->
    <transition name="popup">
      <div v-if="showAddNewBusinessForm" class="absolute top-0 left-0 w-full p-5">
        <form class="bg-white rounded-lg p-5" @submit.prevent="addNewBusiness">
          <div>
            <button type="button" @click="showAddNewBusinessForm = false">
              <img src="@/assets/icons/close.svg" class="w-4" />
            </button>
          </div>

          <div class="text-center text-xl flex items-center justify-center w-full">
            <span
              class="text-xm rounded-full qr-pink-bg w-5 h-5 flex justify-center items-center text-white font-light mr-2"
            >+</span>
            <span class="font-bold mr-2">Add</span> Business
          </div>

          <div class="py-10">
            <div class="mb-5">
              <label for="new_business_name" class="mb-1 inline-block">Business Name</label>
              <input
                v-model="addBusinessForm.business_name"
                type="text"
                placeholder="e.g Jayden Mini Store"
                id="new_business_name"
                required
                autocomplete="off"
                class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-12 text-md placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
              />
            </div>
            <div class="mb-8">
              <label for="email" class="mb-1 inline-block">Business Location</label>
              <input
                v-model="addBusinessForm.business_location"
                type="text"
                placeholder="e.g Muthaiga Estate"
                id="new_business_location"
                required
                autocomplete="off"
                class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-12 text-md placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
              />
            </div>
            <div class="mb-5 flex justify-center">
              <button
                class="w-full p-3 px-5 border border-pink-700 rounded-full shadow-md h-14 font-semibold text-md placeholder-black focus:border-teal-500 focus:outline-none tracking-wider qr-btn text-white"
              >ADD BUSINESS</button>
            </div>
          </div>
        </form>
      </div>
    </transition>
    <!-- Add New Business End -->

    <!-- Edit Business -->
    <transition name="popup">
      <div v-if="showEditBusinessForm" class="absolute top-0 left-0 w-full p-5">
        <form class="bg-white rounded-lg p-5" @submit.prevent="editBusiness">
          <div>
            <button type="button" @click="showEditBusinessForm = false">
              <img src="@/assets/icons/close.svg" class="w-4" />
            </button>
          </div>

          <div class="text-center text-xl flex items-center justify-center w-full">
            <span
              class="text-xm rounded-full qr-pink-bg w-5 h-5 flex justify-center items-center text-white font-light mr-2"
            >+</span>
            <span class="font-bold mr-2">Edit</span> Business
          </div>

          <div class="py-10">
            <div class="mb-5">
              <label for="new_business_name" class="mb-1 inline-block">Business Name</label>
              <input
                v-model="editBusinessForm.business_name"
                type="text"
                placeholder="e.g Jayden Mini Store"
                id="new_business_name"
                required
                autocomplete="off"
                class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-12 text-md placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
              />
            </div>
            <div class="mb-8">
              <label for="email" class="mb-1 inline-block">Business Location</label>
              <input
                v-model="editBusinessForm.business_location"
                type="text"
                placeholder="e.g Muthaiga Estate"
                id="new_business_location"
                required
                autocomplete="off"
                class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-12 text-md placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
              />
            </div>
            <div class="mb-5 flex justify-center">
              <button
                class="w-full p-3 px-5 border border-pink-700 rounded-full shadow-md h-14 font-semibold text-md placeholder-black focus:border-teal-500 focus:outline-none tracking-wider qr-btn text-white"
              >SAVE CHANGES</button>
            </div>
          </div>
        </form>
      </div>
    </transition>
    <!-- Edit Business End -->

    <!-- Floating Toolbar-->
    <div
      class="toolbar fixed bottom-0 left-0 w-full p-5"
      v-show="showToolbar == true && currentUser.role != 'customer'"
    >
      <div class="shadow-lg mx-auto rounded-md flex justify-between qr-gray-bg">
        <router-link
          to="#"
          :class="{'p-3 flex-1 flex justify-center':true,'qr-pink-bg':currentView == 'home'}"
        >
          <img src="@/assets/icons/home.svg" alt class="w-6" width="24px" height="24px" />
        </router-link>

        <button @click.prevent="scanQR()" :class="{'p-3 flex-1 flex justify-center':true}">
          <img src="@/assets/icons/qr-code.svg" alt class="w-6" width="24px" height="24px" />
        </button>

        <router-link to="#" :class="{'p-3 flex-1 flex justify-center':true}">
          <img src="@/assets/icons/menu-user.svg" alt class="w-6" width="24px" height="24px" />
        </router-link>

        <router-link to="#" :class="{'hidden p-3 flex-1 flex justify-center':true}">
